.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color:$form-background;
  font-weight: 700;
  text-transform: uppercase;
  z-index: 1;

  &__top {
    margin-bottom: 32px;

    position: sticky;
    z-index: 1;
    background-color: $form-background;

    @include onTablet{
      margin-bottom: 0;
    }
  }

  &__content {
    @include contentPaddingInline;
  }

  &__phone__number {
  display: block;
  color: inherit;

  text-decoration: none;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 2px;
  margin-bottom: 16px;
  }

  &__call-to-order {
  display: block;
  color: inherit;
  padding-bottom: 3px;
  border-bottom: solid $main-text-color;
  width: fit-content;

  text-decoration: none;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 2px;
  }
}

