.section {
  margin: 20px;
  margin-top: 64px;

  @include onTablet {
    margin: 72px;
    margin-bottom: 0;
    margin-top: 120px;
  }

  @include onDesktop {
    margin-bottom: 0;
    margin-top: 120px;
  }

  &__header {
    width: 100%;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    padding-bottom: 48px;

  @include onTablet {
    font-size: 40px;
    line-height: 40px;
    padding-bottom: 56px;
  }

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
    padding-bottom: 56px;
  }
}
}
